import React from "react";
import { Typography } from "antd";
import { Flexbox } from "src/components";

const MainPage = () => {
  return (
    <Flexbox
      width="100%"
      height="calc(100vh - 64px - var(--spacing-default))"
      justify="center"
    >
      <Typography.Title level={3} style={{ textAlign: "center" }}>
         -      
        
      </Typography.Title>
    </Flexbox>
  );
};

export default MainPage;
